@import url('https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@500&display=swap');
.Info {
    background: hsl(30, 50, 50);
    color: white;
    font-size: 3rem;
    font-family: 'JetBrains Mono', monospace;
    display: flex;
    flex-flow: column;
    flex: none;
}

.Info > span {
    display: block;
    margin: 2rem;
    transition: all 1s ease;
}

.Info-full-btn {
    font-family: inherit;
    font-weight: bold;
    appearance: none;
    font-size: 1.5rem;
    margin: 3rem auto;
    padding: 2rem 3rem;
    border-radius: 10px;
    border: none;
    transition: all 1s ease-out;
}

.Info-full-btn:hover {
    box-shadow: 0px 0px 0px 2px rgba(0, 0, 255, .7);
}

.Info-full-btn:focus {
    outline: none;
    box-shadow: 0px 0px 0px 4px rgba(42, 134, 255, 0.7);;
}

.light {
    background: rgba(0, 0, 0, 0);
    color: white;
    border: solid 5px white;
}

.dark {
    background: rgba(0, 0, 0, 0);
    color: black;
    border: solid 5px black;
}
